import { getCallById } from '../../../interface/selectors'
import { CALL_STATES } from '../../../constants'
import BasicError, { callCodes } from '../../../../errors'

/*
 * Pre-operation validation method.
 *
 * @param {Object} state The SDK state.
 * @param {Object} callId The ID the SDK uses to identify the call.
 * @return {BasicError|undefined} If operation cannot be performed, returns an error.
 */

export default function validate(state, callId, filePath, onPlayingCallback) {
  const call = getCallById(state, callId)
  /*
   * `playAudioFile` operation validation:
   *    1. Call must exist.
   *    2. Call must be in `Connected` state.
   */
  if (!call) {
    return new BasicError({
      code: callCodes.INVALID_PARAM,
      message: 'Call state not found; invalid call ID.'
    })
  } else if (call.state !== CALL_STATES.CONNECTED) {
    return new BasicError({
      code: callCodes.INVALID_STATE,
      message: `Invalid call state: ${call.state}. It should be: ${CALL_STATES.CONNECTED}.`
    })
  }
}
