// Call plugin.
import { CALL_STATES } from '../../../constants'
import { OP_STATUS, OPERATIONS, OP_TRANSITIONS } from '../../../interface/constants'
import * as eventTypes from '../../../interface/eventTypes'
import * as actionTypes from '../../../interface/actionTypes'
import { callActions as actions } from '../../../interface/actions'
import { getCallById, getCallByWrtcsSessionId } from '../../../interface/selectors'
import { operationMap } from '../operationMap'

import { getMediaState } from '../../../utils/state'
import createGetOperation from '../../utils/remoteOperation'

// Call Reports.
import { REPORTER_OPERATION_EVENTS_MAP } from '../../reporter/constants'

export default function createFlow(container) {
  const { context, Callstack, Notifications, CallReporter, emitEvent, logManager } = container
  const getRemoteOperationInfo = createGetOperation(container)

  async function newRemoteOperation(call, params) {
    const log = logManager.getLogger('CALL', call.id)
    const callReport = CallReporter.getReport(call.id)

    /*
     * No existing on-going operation; it is a new remote op.
     */
    const isSlowStart = !params.sdp
    let opInfo
    if (isSlowStart) {
      opInfo = { remoteOp: OPERATIONS.SLOW_START }
    } else {
      opInfo = await getRemoteOperationInfo(call, params.sdp)
    }

    const eventName = REPORTER_OPERATION_EVENTS_MAP[opInfo.remoteOp + '_REMOTE']
    const operationEvent = callReport.addEvent(eventName)
    context.dispatch(
      actions.operationUpdate(call.id, opInfo.remoteOp, false, {
        transition: OP_TRANSITIONS.START,
        eventId: operationEvent.id
      })
    )

    // Emit "operation start" event for the application.
    emitEvent(eventTypes.CALL_OPERATION, {
      callId: call.id,
      isLocal: false,
      operation: opInfo.remoteOp,
      transition: OP_TRANSITIONS.START,
      previous: undefined
    })

    // Get the new call state after starting the remote operation
    call = getCallById(context.getState(), call.id)
    try {
      const opName = operationMap[opInfo.remoteOp]
      const mediaState = getMediaState(call)
      log.info(`Processing update request as remote ${opInfo.remoteOp} operation in ${mediaState} scenario.`)

      await Callstack.operations[opName].remoteOffer(call, params, opInfo)
    } catch (err) {
      // Update call state to indicate that the remote operation is finished since it failed.
      context.dispatch(
        actions.operationUpdate(call.id, opInfo.remoteOp, false, {
          transition: OP_TRANSITIONS.FINISH,
          eventId: operationEvent.id
        })
      )

      operationEvent.setError(err)
      operationEvent.endEvent()

      // Emit "operation finish" event for the application.
      emitEvent(eventTypes.CALL_OPERATION, {
        callId: call.id,
        isLocal: false,
        operation: opInfo.remoteOp,
        transition: OP_TRANSITIONS.FINISH,
        previous: {
          operation: opInfo.remoteOp,
          status: OP_STATUS.ONGOING
        }
      })

      // TODO: Either here or in the operation, respond to Gateway that
      //    operation failed, so that the remote endpoint knows.
      return
    }

    // If this isn't a slow start operation we can end the event here.
    if (!isSlowStart) {
      operationEvent.endEvent()
    }

    // Regular operations are finished now, but slow-start are not.
    const opTransition = isSlowStart ? OP_TRANSITIONS.UPDATE : OP_TRANSITIONS.FINISH

    context.dispatch(
      actions.operationUpdate(call.id, opInfo.remoteOp, false, {
        transition: opTransition,
        eventId: operationEvent.id
      })
    )

    // Emit "operation finish" event for the application.
    emitEvent(eventTypes.CALL_OPERATION, {
      callId: call.id,
      isLocal: false,
      operation: opInfo.remoteOp,
      transition: opTransition,
      previous: {
        operation: opInfo.remoteOp,
        status: OP_STATUS.ONGOING
      }
    })
  }

  async function existingLocalOperation(call, params) {
    const log = logManager.getLogger('CALL', call.id)
    /*
     * There is an on-going local operation. Possible glare scenario.
     */
    const isPreEstablish = [CALL_STATES.RINGING, CALL_STATES.INITIATED, CALL_STATES.EARLY_MEDIA].includes(call.state)
    const hasPendingMake = call.currentOperations.some(op => {
      return op.type === OPERATIONS.MAKE && op.status === OP_STATUS.PENDING
    })

    if (isPreEstablish && hasPendingMake) {
      /*
       * Special-case Scenario: We have received a remote offer for a midcall operation
       *    before the call has been established. This can happen when the remote
       *    endpoint performs an operation immediately after they answer the call,
       *    and the notifications are received in the wrong order.
       *
       * The call is waiting for the call answer, so delay the new request until
       *    that is processed. This is a timing issue where the update is received
       *    too early.
       *
       * Reference: KJS-1152
       */
      delayMidCall(call, params)
    } else {
      // Receive offer SDP when there is already an on-going negotiation (glare scenario).
      log.warn('Received offer SDP with an on-going negotiation; ignoring.')
      // KJS-288 TODO: Report glare scenario to Gateway.
    }
  }

  /**
   * Workaround for existingLocalOperation:
   *    If a midcall negotiation is received before the call answer, this method
   *    will delay the midcall negotiation until after the call is established.
   *    On a time-out of 3 seconds.
   * @method delayMidCall
   * @param {string} wrtcsSessionId Id that was provided to `remoteOffer`.
   * @param {Object} params Params that were provided to `remoteOffer`.
   */
  async function delayMidCall(call, params) {
    const log = logManager.getLogger('CALL', call.id)

    function callStartPattern(action) {
      // Wait for the call to be established or ended.
      return (
        [actionTypes.MAKE_CALL_FINISH, actionTypes.END_CALL_FINISH].includes(action.type) &&
        action.payload.id === call.id
      )
    }

    try {
      await Notifications.takeAction(callStartPattern, 3000)
      log.info('Replaying delayed update request.', { wrtcsSessionId: params.wrtcsSessionId })
      // Call back into remoteOffer to start again.
      return await remoteOffer(call.wrtcsSessionId, params)
    } catch (err) {
      log.info('Timed-out delayed update request. Ignoring.', { wrtcsSessionId: params.wrtcsSessionId })
      // TODO: Send 491 response.
    }
  }

  async function remoteOffer(wrtcsSessionId, params) {
    const call = getCallByWrtcsSessionId(context.getState(), wrtcsSessionId)

    const log = logManager.getLogger('CALL', call ? call.id : undefined)
    log.info('Received new update call request; handling.', { wrtcsSessionId })

    // TODO: Is this check needed? The middleware already verifies if the call exists or not.
    if (!call) {
      // Scenario: No call is associated with the wrtcsSessionId.
      log.info('Update request is for unknown wrtcsSession. Ignoring.', { wrtcsSessionId })
      return
    } else if (call.state === CALL_STATES.ENDED) {
      // Scenario: The associated call is ended, and should not have an active
      //    WebRTC session.
      log.info('Update request is for ended call. Ignoring.', { wrtcsSessionId })
      return
    }

    // Dispatch a custom parameters received action/event if any custom parameters were received as part of the notification
    if (params.customParameters) {
      const customParameters = params.customParameters
      const customNames = customParameters.map(param => param.name)

      log.debug(`Received custom parameters as part of the Call: ${customNames}.`)
      context.dispatch(
        actions.customParametersReceived(call.id, {
          customParameters
        })
      )
      // Emit the associated event with the above action
      emitEvent(eventTypes.CUSTOM_PARAMETERS, { callId: call.id, customParameters })
    }

    if (call.currentOperations.length === 0) {
      return await newRemoteOperation(call, params)
    } else if (call.currentOperations.some(op => op.isLocal)) {
      return await existingLocalOperation(call, params)
    } else {
      log.warn('Receive new remote operation during on-going remote opration.')
    }
  }

  return remoteOffer
}
