import { CALL_STATES } from '../../../constants'
import { getCallById } from '../../../interface/selectors'
import BasicError, { callCodes } from '../../../../errors'

/*
 * Pre-operation validation method.
 *
 * @param {Object} call The call being operated on.
 * @return {BasicError|undefined} If operation cannot be performed, returns an error.
 */

export default function validate(state, callId, otherCallId, newCallId, from) {
  const primaryCall = getCallById(state, callId)
  const secondaryCall = getCallById(state, otherCallId)
  /*
   * `join` operation validation:
   *    1. First Call must exist.
   *    2. Second Call must exist.
   *    3. First Call must be in `On Hold` state.
   *    4. Second Call must be in `On Hold` state.
   */
  if (!primaryCall) {
    return new BasicError({
      code: callCodes.INVALID_PARAM,
      message: 'Call state not found; invalid call ID.'
    })
  } else if (!secondaryCall) {
    return new BasicError({
      code: callCodes.INVALID_PARAM,
      message: 'Call state not found; invalid call ID.'
    })
  } else if (primaryCall.state !== CALL_STATES.ON_HOLD) {
    return new BasicError({
      code: callCodes.INVALID_STATE,
      message: `Call is in an invalid state: state=${primaryCall.state}. It should be: state=${CALL_STATES.ON_HOLD}.`
    })
  } else if (secondaryCall.state !== CALL_STATES.ON_HOLD) {
    return new BasicError({
      code: callCodes.INVALID_STATE,
      message: `Call is in an invalid state: state=${secondaryCall.state}. It should be: state=${CALL_STATES.ON_HOLD}.`
    })
  }
}
