// Call plugin.
import { callActions as actions } from '../../../interface/actions'
import * as eventTypes from '../../../interface/eventTypes'
import { OP_TRANSITIONS, OP_STATUS } from '../../../interface/constants'
import { CALL_STATES } from '../../../constants'
import { getCallByWrtcsSessionId } from '../../../interface/selectors'

// Call Reports.
import { REPORTER_OPERATION_EVENTS_MAP } from '../../reporter/constants'

export default function createFlow(container) {
  const { context, CallReporter, emitEvent } = container

  /*
   * Operation wrapper for handling remote notifications.
   * TODO: Split out the "responses from local operation" notifications.
   */
  return async function remoteOnly(wrtcsSessionId, handler, params) {
    let call = getCallByWrtcsSessionId(context.getState(), wrtcsSessionId)

    let notificationEvent

    // See if there is a pending, local operation.
    const pendingLocal = call.currentOperations.find(op => {
      return op.isLocal && op.status === OP_STATUS.PENDING
    })

    // TODO: Handle all notifications instead of only the ones the reporter knows about.
    // If the call exists (in the sense that is both valid as an object AND it's state is not ended)
    if (call && call.state !== CALL_STATES.ENDED) {
      if (pendingLocal && pendingLocal.eventId && REPORTER_OPERATION_EVENTS_MAP.hasOwnProperty(handler.name)) {
        // if call has an on-going local operation, then the
        // notification is part of that operation.

        // Get the local main event
        const operationEvent = CallReporter.getReport(call.id).getEvent(pendingLocal.eventId)
        // create & add a local sub-event
        notificationEvent = operationEvent.addEvent(REPORTER_OPERATION_EVENTS_MAP[handler.name])
      } else if (REPORTER_OPERATION_EVENTS_MAP.hasOwnProperty(handler.name)) {
        // Otherwise, if the call exists (but there isn't an on-going local operation),
        //    the notification is part of a new remote operation.
        const report = CallReporter.getReport(call.id)
        notificationEvent = report.addEvent(REPORTER_OPERATION_EVENTS_MAP[handler.name])
      }
    } else {
      // If the call doesn't exist, can't add anything to its report. TODO.
    }

    await handler(wrtcsSessionId, ...params)

    // End the sub-event from handling the notification.
    notificationEvent && notificationEvent.endEvent()

    // Get the call again for new state after the handler has run
    call = getCallByWrtcsSessionId(context.getState(), wrtcsSessionId)
    // If this notification is an answer from a local operation, update call state
    //    to indicate that the local operation is finished.
    if (
      pendingLocal &&
      [
        'sessionStatusEnded', // Complex operation success.
        'callStatusUpdateFailed', // Complex operation failure.
        'callStatusUpdateEnded' // Call end notification mid-operation.
      ].includes(handler.name)
    ) {
      const eventId = pendingLocal.eventId
      context.dispatch(
        actions.operationUpdate(call.id, pendingLocal.type, true, {
          transition: OP_TRANSITIONS.FINISH,
          eventId
        })
      )

      // End the timeline event for the local operation.
      const operationEvent = CallReporter.getReport(call.id).getEvent(eventId)
      operationEvent.endEvent()

      // Emit "operation finish" event for the application.
      emitEvent(eventTypes.CALL_OPERATION, {
        callId: call.id,
        isLocal: true,
        operation: pendingLocal.type,
        transition: OP_TRANSITIONS.FINISH,
        previous: {
          operation: pendingLocal.type,
          status: OP_STATUS.PENDING
        }
      })
    }
  }
}
