import newOutgoing from './newOutgoing'
import newIncoming from './newIncoming'
import localOperation from './localOperation'
import remoteOffer from './remoteOffer'
import remoteAnswer from './remoteAnswer'
import remoteOnly from './remoteOnly'
import dualCall from './dualCall'

export default function setupFlows(container) {
  return {
    newOutgoing: newOutgoing(container),
    newIncoming: newIncoming(container),
    localOperation: localOperation(container),
    remoteOffer: remoteOffer(container),
    remoteAnswer: remoteAnswer(container),
    remoteOnly: remoteOnly(container),
    dualCall: dualCall(container)
  }
}
