// Operations.
import localAnswer from './local/answer'
import validate from './local/validate'

import slowAnswer from './slow/slowAnswer'
import slowValidate from './slow/validate'
import remoteSlowAnswer from './slow/remoteSlowAnswer'

// Helpers
import answerWebrtcSession from './answerWebrtcSession'

import { OPERATIONS } from '../../../interface/constants'

export default function initOperation(bottle) {
  // Provide the top-level container to the factory functions.
  //    Otherwise they would get the `operations` sub-container.
  bottle.factory('Callstack.operations.answer', () => {
    const operation = localAnswer(bottle.container)
    // Add meta-data to the operation for the CallManager.
    operation.isNegotiation = false
    operation.type = OPERATIONS.ANSWER
    operation.validate = validate
    return operation
  })

  bottle.factory('Callstack.operations.answerSlow', () => {
    const operation = slowAnswer(bottle.container)
    operation.remoteAnswer = remoteSlowAnswer(bottle.container)
    // Add meta-data to the operation for the CallManager.
    operation.isNegotiation = true
    operation.type = OPERATIONS.ANSWER
    operation.validate = slowValidate
    return operation
  })

  bottle.factory('Callstack.utils.answerWebrtcSession', () => answerWebrtcSession(bottle.container))
}
