// Operations.
import removeMediaOperation from './local/removeMedia'
import removeValidate from './local/validate'

import webrtcRemoveMediaOperation from './webrtcRemoveMedia'
import rollbackOperation from './rollbackRemoveMedia'
import removeBasicMediaOperation from './local/removeBasicMedia'
import basicValidate from './local/validateBasic'

import remoteOffer from './remote/remoteOffer'
import remoteAnswer from './local/remoteAnswer'

import { OPERATIONS } from '../../../interface/constants'

export default function initOperation(bottle) {
  // Provide the top-level container to the factory functions.
  //    Otherwise they would get the `operations` sub-container.
  bottle.factory('Callstack.operations.removeMedia', () => {
    const operation = removeMediaOperation(bottle.container)
    operation.remoteOffer = remoteOffer(bottle.container)
    operation.remoteAnswer = remoteAnswer(bottle.container)
    // Add meta-data to the operation for the CallManager.
    operation.isNegotiation = true
    operation.type = OPERATIONS.REMOVE_MEDIA
    operation.validate = removeValidate
    return operation
  })

  bottle.factory('Callstack.operations.removeBasicMedia', () => {
    const operation = removeBasicMediaOperation(bottle.container)
    operation.remoteAnswer = remoteAnswer(bottle.container)
    // Add meta-data to the operation for the CallManager.
    operation.isNegotiation = true
    operation.type = OPERATIONS.REMOVE_BASIC_MEDIA
    operation.validate = basicValidate
    return operation
  })

  bottle.factory('Callstack.utils.webrtcRemoveMedia', () => webrtcRemoveMediaOperation(bottle.container))
  bottle.factory('Callstack.utils.rollbackRemoveMedia', () => rollbackOperation(bottle.container))
}
