// Call plugin.
import { callActions as actions } from '../../interface/actions'
import * as eventTypes from '../../interface/eventTypes'
import { OP_TRANSITIONS, OP_STATUS, OPERATIONS } from '../../interface/constants'

// Call Reports.
import { REPORTER_OPERATION_EVENTS_MAP } from '../reporter/constants'

import { v4 as uuidV4 } from 'uuid'

/*
 * Factory for an "operation tracker".
 * An "operation tracker" is a utility object for managing call state updates
 *    and events for operation tracking.
 *
 * Each function is a code-block that was repeated numerous times in the
 *    CallManager. So it was split out to reduce noise.
 * The fact that this utility exists is a sign that how we do this is bad.
 * @param {Object} [joinOptions] Edge-case parameter when the operation is created elsewhere.
 */
export default function createTracker(container, callId, operation, isLocal, joinOptions) {
  const tracker = {
    status: undefined,
    id: uuidV4()
  }

  if (operation.type === OPERATIONS.JOIN && joinOptions && joinOptions.id === 'joined-call-initiate') {
    // JOIN is a special-case. A newly joined call's `tracker.start()` method will not be called,
    //    so initialize the tracker in an already started state. The ID is hardcoded in the call
    //    reducer for a newly, joined call. The join operation needs to be changed for this to
    //    be fixed.
    tracker.status = OP_STATUS.ONGOING
    tracker.id = joinOptions.id
    tracker.opEvent = joinOptions.opEvent
  }

  tracker.start = () => {
    if (tracker.status) {
      throw new Error('Operation tracking has already been started.')
    }

    tracker.opEvent = startOperation(container, callId, tracker.id, operation, isLocal)
    tracker.status = OP_STATUS.ONGOING
    return tracker.opEvent
  }

  tracker.update = result => {
    if (tracker.status !== OP_STATUS.ONGOING) {
      throw new Error('Operation tracking cannot be updated.')
    }
    updateOperation(container, tracker.opEvent, callId, tracker.id, operation, isLocal, result)
    tracker.status = OP_STATUS.PENDING
  }

  tracker.finish = err => {
    if (![OP_STATUS.ONGOING, OP_STATUS.PENDING].includes(tracker.status)) {
      throw new Error('Operation tracking cannot be finished.')
    }

    finishOperation(container, tracker.opEvent, callId, tracker.id, operation, isLocal, err)
    tracker.status = 'FINISHED'
  }

  return tracker
}

export function startOperation(container, callId, operationId, operation, isLocal) {
  const { context, CallReporter, emitEvent } = container
  const { type, isNegotiation } = operation

  const callReport = CallReporter.getReport(callId)
  const opEvent = callReport.addEvent(REPORTER_OPERATION_EVENTS_MAP[type])

  // Update call state to indicate that an operation is starting.
  context.dispatch(
    actions.operationUpdate(callId, type, isLocal, {
      transition: OP_TRANSITIONS.START,
      eventId: opEvent.id,
      isBlocking: isNegotiation,
      operationId
    })
  )

  // Emit "operation start" event for the call.
  emitEvent(eventTypes.CALL_OPERATION, {
    callId,
    operationId,
    isLocal,
    operation: type,
    transition: OP_TRANSITIONS.START,
    previous: undefined
  })

  return opEvent
}

export function updateOperation(container, opEvent, callId, operationId, operation, isLocal, result) {
  const { context, emitEvent } = container

  context.dispatch(
    actions.operationUpdate(callId, operation.type, isLocal, {
      transition: OP_TRANSITIONS.UPDATE,
      operationData: result && result.operationData ? result.operationData : undefined,
      eventId: opEvent.id,
      operationId
    })
  )

  // Emit "operation update/finish" event for the call.
  emitEvent(eventTypes.CALL_OPERATION, {
    callId,
    operationId,
    isLocal,
    operation: operation.type,
    transition: OP_TRANSITIONS.UPDATE,
    previous: {
      operation: operation.type,
      status: OP_STATUS.ONGOING
    }
  })
}

export function finishOperation(container, opEvent, callId, operationId, operation, isLocal, err) {
  const { context, emitEvent } = container

  // Update call state to indicate the operation is finished, since it failed.
  context.dispatch(
    actions.operationUpdate(callId, operation.type, isLocal, {
      transition: OP_TRANSITIONS.FINISH,
      eventId: opEvent.id,
      operationId
    })
  )

  // Emit "operation finish" event for the application.
  emitEvent(eventTypes.CALL_OPERATION, {
    callId,
    operationId,
    isLocal,
    operation: operation.type,
    transition: OP_TRANSITIONS.FINISH,
    error: err,
    previous: {
      operation: operation.type,
      status: OP_STATUS.ONGOING
    }
  })
}
